.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  background-color: black;
}

.header {
  width: 100%;
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-bottom: 2rem;
  position: sticky;
  top: 0;
  background-color: black;
  border: solid 2px #fff;
  border-radius: 1rem;
  box-shadow: 0 0 0.2rem #fff, 0 0 0.2rem #fff, 0 0 2rem #bc13fe,
    0 0 0.8rem #bc13fe, 0 0 2.8rem #bc13fe, inset 0 0 1.3rem #bc13fe;
  cursor: pointer;
  user-select: none;
  z-index: 99;
}
.content {
  width: 100%;
}

@media screen and (max-width: 768px) {
  .header {
    position: static;
    margin-bottom: 1rem;
  }
}
