.card {
  display: flex;
  flex-direction: column;
  margin: 1rem;
  border: solid 1px #fff;
  border-radius: 0.5rem;
  box-shadow: 0 0 0.2rem #fff, 0 0 0.2rem #fff, 0 0 2rem #bc13fe;
  cursor: pointer;
  transition: ease-out 400ms;
}
.card:hover {
  transform: scale(1.03);
}
.aTag {
  text-decoration: none;
  color: white;
}
.aTag:hover {
  color: #fff;
  text-shadow:
    /* white glow */ 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff,
    /* green glow */ 0 0 42px var(--main-color-scheme),
    0 0 82px var(--main-color-scheme), 0 0 92px var(--main-color-scheme),
    0 0 102px var(--main-color-scheme), 0 0 151px var(--main-color-scheme);
}

.header {
  padding: 0.5rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.likeButton {
  margin-right: 0.5rem;
  cursor: pointer;
}
.emptyLikeIcon:hover {
  color: red;
}
.filledLikeIcon {
  color: red;
}

.likeCount {
  text-align: center;
  line-height: 100%;
}
.imageContainer {
  border-radius: 0.3rem;
  padding: 1rem;
  max-width: 300px;
  max-height: 300px;
}

.image {
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
}

.footer {
  text-align: center;
  font-family: "Comic Neue", cursive;
  padding: 0.5rem 1rem;
  display: flex;
  flex-direction: column;
}

.name {
  font-size: 1.3rem;
}
