.background {
  width: 100%;
  display: flex;
  justify-content: center;
}

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo {
  width: 100%;
  max-width: 600px;
  max-height: 600px;
}

.quote {
  width: 100%;
  text-align: center;
  font-size: 2rem;
  padding: 1rem;
  font-family: "Comic Neue", cursive;
}

@media screen and (max-width: 768px) {
  .quote {
    font-size: 1.5rem;
    overflow: auto;
  }
}
