.detailsContainer {
  margin-top: 1rem;
  display: grid;
  grid-template-columns: 3fr 1fr 1fr 1fr 1fr;
}
.header {
  text-align: center;
  font-size: 1.5rem;
  font-family: "Comic Neue", cursive;
}

.columns {
  padding: 1rem;
  border-right: solid 1px var(--main-color-scheme);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.shade {
  background: rgb(31, 27, 27);
}

.icecream {
  color: rgb(11, 245, 23);
}

@media screen and (max-width: 768px) {
  .columns {
    padding: 0.5rem;
  }
  .header {
    font-size: 1rem;
    padding: 0 0.5rem;
  }
}
