.body {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.article {
  font-size: 1.5rem;
  text-align: center;
  margin-bottom: 1rem;
}

.checkInfluencerContainer {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0 2rem;
  max-width: 1000px;
}

.warning {
  margin: 0 0.5rem;
  color: orange;
}
.queryLimit {
}

.searchBox {
  margin-top: 1rem;
}

.buttonContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.spinner {
  margin-top: 2rem;
}

.tweetListBox {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.ad {
  width: 100%;
}

@media screen and (max-width: 768px) {
  .article {
    font-size: 1rem;
    margin: 0;
  }
  .checkInfluencerContainer {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 0 2rem;
    height: 250px;
    overflow: scroll;
    margin: 1rem 0;
    background-color: rgb(39, 39, 39);
    border-radius: 10px;
  }
}
