.bodyContainer {
  display: flex;
  flex-direction: column;
}

.body {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.category {
  margin: 2rem 2rem;
  flex-basis: 50%;
}

.categoryNavContainer {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 1rem;
  border: 2px solid var(--main-color-scheme);
  border-radius: 10px;
}
.categoryBox {
  text-align: center;
  padding: 0 1rem;
  border-radius: 0.2rem;
  box-shadow: 0 0 0.2rem #fff, 0 0 0.2rem #fff, 0 0 0.8rem #bc13fe;
  cursor: pointer;
}
.categoryBox:hover {
  transform: scale(0.95);
}

.checkItemContainer {
  background-color: rgb(39, 39, 39);
  margin: 1rem 0;
  padding: 1rem;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  gap: 1rem;
  flex-basis: 50%;
  border-radius: 10px;
}

.canvasWrapper {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.canvasContainer {
  width: 500px;
  height: 500px;
  position: relative;
  background-color: honeydew;
  border-radius: 0.5rem;
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
}
.baseCanvasRef {
  z-index: 0;
}
.backgroundCanvasRef {
  z-index: 1;
}
.bodyCanvas {
  z-index: 2;
}
.skinCanvas {
  z-index: 3;
}
.hairstyleCanvas {
  z-index: 4;
}
.eyeCanvas {
  z-index: 5;
}
.mouthCanvas {
  z-index: 6;
}
.clothesCanvas {
  z-index: 7;
}
.necklaceCanvas {
  z-index: 8;
}
.icecreamCanvas {
  z-index: 9;
}
.watermarkCanvas {
  z-index: 10;
}

.downloadButton {
  background: transparent;
  outline: none;
  color: white;
  width: 300px;
  height: 30px;
  text-align: center;
  border: solid 2px #bc13fe;
  border-radius: 0.2rem;
  box-shadow: 0 0 0.2rem #fff, 0 0 0.2rem #fff, 0 0 2rem #bc13fe,
    0 0 0.8rem #bc13fe;
  margin-top: 1rem;
  user-select: none;
  cursor: pointer;
}
.downloadButton:hover {
  transform: scale(0.95);
}

.download {
  text-align: center;
  font-weight: bold;
}
.hidden {
  display: none;
}
.layerContainer {
  margin: 0 2rem;
  text-align: center;
  width: 200px;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  border-radius: 0.5rem;
  box-shadow: 0 0 0.2rem #fff, 0 0 0.2rem #fff, 0 0 2rem #bc13fe,
    0 0 0.8rem #bc13fe;
}

.text {
  font-size: 1.3rem;
  margin: 0.3rem;
}
.text2 {
  font-size: 1rem;
}
.text,
.text2 {
  text-align: center;
}
.planContainer {
  display: flex;
  justify-content: center;
}
.plan {
  font-size: 1.5rem;
  margin: 0.5rem 1rem;
  display: flex;
  align-items: center;
  gap: 0.3rem;
}
.valid {
  cursor: pointer;
}

.payment {
  margin-top: 1rem;
  margin-left: 1rem;
}
.active {
  color: #fff;
  text-shadow:
    /* white glow */ 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff,
    /* green glow */ 0 0 42px var(--main-color-scheme),
    0 0 82px var(--main-color-scheme), 0 0 92px var(--main-color-scheme),
    0 0 102px var(--main-color-scheme), 0 0 151px var(--main-color-scheme);
}

.ad {
  width: 100%;
}

@media screen and (max-width: 768px) {
  .body {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
  }

  .category {
    margin: 0;
    padding: 1rem;
  }
  .canvasContainer {
    width: 300px;
    height: 300px;
  }

  .checkItemContainer {
    margin: 1rem 0;
    padding: 1rem 1rem;
    width: 100%;
    height: 150px;
    overflow: scroll;
  }

  .canvasWrapper {
    position: sticky;
    top: 0;
    background-color: black;
    border-radius: 10px;
    margin: 0;
  }

  .text,
  .text2 {
    font-size: 1rem;
    overflow-wrap: break-word;
    word-wrap: break-word;
  }
  .payment {
  }
}
