.header {
  text-align: center;
}

/* Neon Text */
.logo {
  text-decoration: none;
  font-weight: bold;
  margin-bottom: 2rem;
  cursor: pointer;
  transition: ease-out 300ms;
  user-select: none;
  font-size: 4rem;
  color: #fff;
  text-shadow:
    /* white glow */ 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff,
    /* green glow */ 0 0 42px var(--main-color-scheme),
    0 0 82px var(--main-color-scheme), 0 0 92px var(--main-color-scheme),
    0 0 102px var(--main-color-scheme), 0 0 151px var(--main-color-scheme);
}

.logo:hover {
  color: rgb(100, 90, 90);
}

.aTag {
  text-decoration: none;
  color: white;
}

.aTag:hover {
  transform: scale(1.05);
  color: #fff;
  text-shadow:
    /* white glow */ 0 0 7px #fff,
    0 0 10px #fff /* green glow */ 0 0 42px var(--main-color-scheme),
    0 0 82px var(--main-color-scheme), 0 0 92px var(--main-color-scheme);
}

.navContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 1rem;
  font-weight: bold;
}

.active {
  color: #fff;
  text-shadow:
    /* white glow */ 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff,
    /* green glow */ 0 0 42px var(--main-color-scheme),
    0 0 82px var(--main-color-scheme), 0 0 92px var(--main-color-scheme),
    0 0 102px var(--main-color-scheme), 0 0 151px var(--main-color-scheme);
}

.navBar {
  display: none;
  cursor: pointer;
  font-size: 2rem;
}
.hidden {
  display: none;
}

@media screen and (max-width: 768px) {
  .logo {
    font-size: 2.5rem;
    margin-bottom: 1rem;
  }
  .button {
    font-size: 0.8rem;
  }
  .navContainer {
    margin-top: 1rem;
    flex-direction: column;
    gap: 1rem;
  }
  .header {
    justify-content: space-between;
  }
  .navBar {
    display: block;
    margin-top: 1rem;
  }
}
