.container {
  max-width: 45rem;
  display: flex;
  font-size: 1.2rem;
  flex-direction: column;
  transition: ease-out 500ms;
  margin: 2rem;
}

.container {
  border: solid 1px white;
  border-radius: 1.5rem;
  box-shadow: 0 0 0.2rem #fff, 0 0 0.2rem #fff, 0 0 2rem #bc13fe,
    0 0 0.8rem #bc13fe, 0 0 2.8rem #bc13fe, inset 0 0 1.3rem #bc13fe;
}
.container:hover {
  cursor: pointer;
  transform: scale(1.05);
}
.link {
  text-decoration: none;
  color: white;
}
.link:hover {
  color: white;
}
.profileBox {
  display: flex;
  align-items: center;
  justify-content: center;
}

.textBox {
  text-align: center;

  overflow-wrap: break-word;
  margin: 2rem 0.5rem;
}

.countBox {
  display: flex;
  justify-content: space-around;
}

.retweetBox,
.replyBox,
.likeBox,
.quoteBox {
  font-size: 1rem;
  text-align: center;
}

.picture {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  padding: 0.5rem;
  margin-left: 1rem;
}
.nameUsernameContainer {
  padding: 0.5rem;
}

.name {
  font-weight: bold;
}

.username {
  color: grey;
}

.createdAt {
  font-size: 1rem;
  padding: 1rem 0;
}

.retweetCount,
.replyCount,
.likeCount,
.quoteCout {
  padding-top: 0.3rem;
}

@media screen and (max-width: 768px) {
  .profileBox,
  .countBox {
    flex-direction: column;
  }
}
