.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.submitted {
  background-color: rgb(39, 39, 39);
  border-radius: 10px;
  font-size: 4rem;
  padding: 1rem 2rem;
  text-shadow:
    /* white glow */ 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff;
}

.form {
  color: white;
}
.inputContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.firstName,
.lastName,
.email,
.phone,
.file,
.message {
  display: flex;
  width: 100%;
  flex-direction: column;
  margin-bottom: 1rem;
}
.required {
  color: red;
}

.inputBox {
  border-radius: 4px;
  height: 2.5rem;
  padding: 0 0.5rem;
}

.textarea {
  height: 10rem;
}

.uploadedImg {
  margin-left: 1rem;
  border-radius: 4px;
}
.ad {
  width: 100%;
}
