.pricingCard {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 1rem;
  margin: 0 1rem;
}
.planDetailContainer {
  margin: 0 1rem;
}

.header {
}

.planContainer {
  border: solid 1px white;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.planName,
.planPrice {
  font-family: "Comic Neue", cursive;
}

.planName {
  font-size: 2rem;
}

.planPrice {
  font-size: 1.5rem;
  font-weight: bold;
}

.ad {
  width: 100%;
}

@media screen and (max-width: 768px) {
  .pricingCard {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
  .planDetailContainer {
    margin: 0;
  }
}
